import React from 'react'
import Link from 'gatsby-link'

export default class Navbar extends React.Component {
  render() {
    const active_tab = this.props.active_tab;

    return (
      <nav className="navbar navbar-default navbar-fixed-top" role="navigation">
        <div className="container-fluid">
          <div className="navbar-header">
            <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#collapse-target-menu" type="button" id="menu-btn-mage">
              <span className="sr-only">Toggle navigation</span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
            </button>
            <p className="navbar-text" id="title-name">Robert Pienta</p>
          </div>

          <div className="collapse navbar-collapse navbar-responsive-collapse" id="collapse-target-menu">
            <ul className="nav navbar-nav pull-right">
              <li className = { active_tab === 'about' ? 'active' : '' }>
                <Link id="MenuLinkText" to="/">ABOUT</Link>
              </li>
              <li className = { active_tab === 'gallery' ? 'active' : ''}>
                <Link id="MenuLinkText" to="/gallery/">GALLERY</Link>
              </li>
              <li className = { active_tab === 'projs' ? 'active' : ''}>
                <Link id="MenuLinkText" to="/projs/">PROJECTS</Link>
              </li>
              <li className = { active_tab === 'contact' ? 'active' : ''}>
                <Link id="MenuLinkText" to="/contact/">CONTACT</Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
};



